import React from 'react';
import { addCells }  from './counterSlice';
import { useDispatch } from 'react-redux';

export function MountChange() {
  const dispatch = useDispatch();

  const handleChange = (e) => {
    dispatch(addCells(e.target.value));
  }
  return (
    <div className="mount-change">
        <div className="form_toggle">
          <div className="form_toggle-item item-1">
            <input onClick={handleChange} id="fid-1" type="radio" name="radio" value="2"/>
            <label htmlFor="fid-1">2x2</label>
          </div>
          <div className="form_toggle-item item-2">
            <input onClick={handleChange} id="fid-2" type="radio" name="radio" value="4"/>
            <label htmlFor="fid-2">4x4</label>
          </div>
          <div className="form_toggle-item item-3">
            <input onClick={handleChange} id="fid-3" type="radio" name="radio" value="6"/>
            <label htmlFor="fid-3">6x6</label>
          </div>
        </div>              
    </div>
    );
}